import React, { useContext } from 'react'
// import 'bootstrap/dist/css/bootstrap.min.css';

import { Redirect } from "react-router-dom"
import "./themes/scss/App.scss"
import "notyf/notyf.min.css"
import "./util.css"
// import dark from "./themes/dark.js";
// import dark_blue from "./themes/dark_blue.js";

// import  "./themes/dark_blue.css";
// import  "./themes/dark.css";

import ThemeContextProvider from "./contexts/theme_context";
import AuthContextProvider, { AuthContext } from "./contexts/auth_context";

import withSplashScreen from "./pages/splashScreen";

import Breakpoint from "./components/breakpoint/breakpoint"

// import Navbar from "./components/navbar"
import Navbar from "./components/Toolbar/toolbar"
import BottomNavbar from "./components/Toolbar/bottomNav"

import Home from "./pages/home"
import Dashboard from "./pages/userDashboard"
// import Loader from "./components/loader"
import Settings from "./pages/settings"
import Login from "./pages/login"
import Register from "./pages/register"
import Edit from "./pages/editorjs"
import Read from "./pages/readArticles"
import Status from "./pages/status"
import Page404 from "./pages/404.js"
// import DynamicSheet from "./component/DynamicSheet";

import {
    BrowserRouter as Router,
    Switch,
    Route,
    // Link
} from "react-router-dom";

const testP = (props) => {
    function pageType(p) {
        let t = "unknown";
        if (p !== null && p !== "") {
            if (p.length > 1 && p.charAt(0) === "@") {
                t = "user page"
            } else {
                t = "other"
            }
        }

        return t
    }
    return (<>
        username: {props.match.params.username}<br />
        type: {pageType(props.match.params.username)}
    </>);
}

function AppInnerContainer() {

    // useEffect(() => {
    // document.getElementById("root").style.display = "block";
    // document.getElementById("page-loader").style.display = "none";
    // document.title = "Lattice";
    // });

    const { isLoggedIn } = useContext(AuthContext)

    return (
        <>
            <ThemeContextProvider>
                <Router>
                    <div>
                        <Breakpoint xs sm md lg xl>
                            <Navbar />
                        </Breakpoint>


                        <Breakpoint xs sm>
                            <div className="fixed-bottom">
                                <BottomNavbar />
                            </div>
                        </Breakpoint>

                        <br />
                        <br />
                        <br />
                        {/* <br/> */}
                        <Switch>
                            <Route exact path="/">
                                <Home />
                            </Route>


                            <Route path="/about">
                                {/* <div>About</div> */}
                                <Status />
                            </Route>
                            <Route path="/dashboard">
                                {(() => {
                                    if (isLoggedIn) {
                                        return (<> <Dashboard /> </>)
                                    } else {
                                        return (<> <Redirect to="/login" /> </>)
                                    }
                                })()}
                            </Route>

                            <Route path="/settings">
                                {(() => {
                                    if (isLoggedIn) {
                                        return (<> <Settings /> </>)
                                    } else {
                                        return (<> <Redirect to="/login" /> </>)
                                    }
                                })()}
                            </Route>

                            <Route path="/login">
                                <Login />
                            </Route>

                            <Route path="/register">
                                <Register />
                            </Route>

                            <Route path="/edit">
                                {(() => {
                                    if (isLoggedIn) {
                                        return (<> <Edit /> </>)
                                    } else {
                                        return (<> <Redirect to="/login" /> </>)
                                    }
                                })()}
                            </Route>

                            <Route path="/read">
                                <Read />
                            </Route>

                            {/* <Route path="/e2">
                <Edit2 />
              </Route> */}

                            <Route path="/:username" component={testP} />

                            <Route path="*">
                                <Page404 />
                            </Route>
                        </Switch>

                    </div>

                </Router>

            </ThemeContextProvider>
            {/* </div> */}

        </>
    );
}


const App = () => {
    return (
        <>
            <AuthContextProvider>
                <AppInnerContainer />
            </AuthContextProvider>
        </>
    );
}


export default withSplashScreen(App);
// export default App;
