import React from 'react';

import Axios from "axios"
import { getApiLink } from "../lib/env.lib"
const getStatusDisp = (disp) => {
    let d;
    if (disp === -1) {
        d = (<> <span style={{ color: "yellow" }}>LODING</span></>)
    } else if (disp === 0) {
        d = (<> <span style={{ color: "red" }}>INACTIVE</span></>)
    } else if (disp === 1) {
        d = (<> <span style={{ color: "green" }}>ACTIVE</span></>)
    }

    return d
}
const Status = (props) => {

    const [uiStatus, setUiStatus] = React.useState(-1)

    React.useEffect(() => {
        console.log({ api: getApiLink() })
        Axios.get(`${getApiLink()}/api/v1/user-api-service`).then((response) => {
            console.log(response)
            if (response.data.success) {
                setUiStatus(1)
            }else{
                setUiStatus(0)
            }
        })
    }, [])
    return (
        <>
            <div className="container">
                <h1>Status</h1>

                <p>UI STATUS: {getStatusDisp(uiStatus)}</p>
            </div>
        </>
    );
}

export default Status;