import React, { useRef } from 'react';

import EditorJs from 'react-editor-js';
import ReadMode from "../components/readMode"


import { EDITOR_JS_TOOLS } from "../lib/editorjs.dependencies"


const Editor = () => {
    let key_i = 0;
    const instanceRef = useRef(null);

    const [data, setData] = React.useState(
        { "time": 1587213406738, "blocks": [{ "type": "header", "data": { "text": "Title", "level": 1 } }, { "type": "paragraph", "data": { "text": "this is text<br>" } }, { "type": "table", "data": { "content": [["Table<br>", "r2", "r3"], ["1", "2", "3"], ["4", "5", "6"]] } }, { "type": "paragraph", "data": { "text": "Ordered list" } }, { "type": "list", "data": { "style": "ordered", "items": ["one", "two"] } }, { "type": "paragraph", "data": { "text": "Unordered List" } }, { "type": "list", "data": { "style": "unordered", "items": ["One <br>", "Two"] } }, { "type": "warning", "data": { "title": "Note<br>", "message": "This is a warning<br>" } }, { "type": "code", "data": { "code": "This is some code!!!" } }, { "type": "paragraph", "data": { "text": "This is some inline embed<br>" } }, { "type": "linkTool", "data": { "link": "https://twitter.com/CrabsAndScience/status/1250530983675191297", "meta": { "description": "I trained my cat to pee in the sink. Saving big $$$ on litter ", "title": "William Osman on Twitter", "language": "en", "type": "article", "provider": "Twitter", "image": { "url": "https://pbs.twimg.com/media/EVrHTCcU8AAlfWD.jpg:large" } } } }, { "type": "paragraph", "data": { "text": "Image dosent seem to work rn<br>" } }, { "type": "image", "data": { "file": {}, "caption": "", "withBorder": false, "stretched": false, "withBackground": false } }, { "type": "header", "data": { "text": "Heading 1", "level": 1 } }, { "type": "header", "data": { "text": "Heading 2", "level": 2 } }, { "type": "header", "data": { "text": "Heading 3", "level": 3 } }, { "type": "header", "data": { "text": "Heading 4", "level": 4 } }, { "type": "header", "data": { "text": "Heading 5", "level": 5 } }, { "type": "header", "data": { "text": "Heading 6", "level": 6 } }, { "type": "paragraph", "data": { "text": "this is a para" } }, { "type": "quote", "data": { "text": "Some shittty quote<br>", "caption": "with a shitty caption<br>", "alignment": "left" } }, { "type": "checklist", "data": { "items": [{ "text": "Checklist one!!!", "checked": true }, { "text": "checklist two!!!", "checked": true }] } }, { "type": "delimiter", "data": {} }], "version": "2.17.0" }
    )

    async function handleSave() {
        const savedData = await instanceRef.current.save();

        setData(savedData)
        console.log("savedData", savedData);
    }

    // React.useEffect(() => {
    //     effect
    //     return () => {
    //         cleanup
    //     }
    // }, [input])

    return (<>
        <br />
        {/* <br />
        <br />
        <br /> */}
        <div className="container">
            {/* <p>{JSON.stringify(data)}</p> */}
            <div className="row">

                <div className="col-lg-6" key={1}>
                    <EditorJs
                        placeholder="Write an awesome..."
                        instanceRef={instance => (instanceRef.current = instance)}
                        onChange={handleSave}
                        tools={EDITOR_JS_TOOLS}
                        data={data}
                    >
                        {/* <div id="custom" /> */}
                    </EditorJs>
                </div>

                <div className="col-lg-6" key={2}>
                    <ReadMode key={(key_i++).toString()} data={data} />
                </div>
            </div>
            <hr />

        </div>
    </>
    );
}

export default Editor;